import React from 'react'
import Lottie from 'react-lottie'

export default function LottieAnimation({ lotti, position }) {
  const defaultOptions = {
    loop: true,
    autoplay: true,
    animationData: lotti,
    rendererSettings: {
      preserveAspectRatio: 'xMidYMid slice',
    },
  }

  return (
    <div>
      <Lottie options={defaultOptions} position={position} />
    </div>
  )
}